@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap');
// font-family: 'Roboto', sans-serif;

*{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    &::-webkit-scrollbar{
        display: none;
    }
}

a, h2{
    color: white;
}

a{
    text-decoration: none;
}

$bg: rgb(16, 16, 16);

.header {
    height: 100px;
    background-color: $bg;
    display: flex;
    align-items: center;
    padding: 2rem;
    box-sizing: border-box;
    justify-content: space-between;
    cursor: pointer;

    >img {
        height: 100%;
    }

    >div {
        // border: 1px solid red;
        width: 100%;
        margin-left: 4vmax;

        > a{
            margin: 2vmax;
            font-size: 1.2rem;
            font-weight: 300;
        }
    }

    >svg{
        color: white;
        font-size: 2rem;
        cursor: pointer;
        &:hover{
            color: rgb(195, 189, 189);
        }
    }
}
